@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap);
:root {
  --green: #27ae60;
  --main-bg-color: #eee;
  --second-color: #222;
  --white-color: #fff;
  --third-color:#333;
  --forth-color:#fff;
  --theme:rgb(0, 0, 0);
}
body.active {
  --green: blue;
  --main-bg-color: #222;
  --second-color: rgb(255, 255, 255);
  --white-color: #000;
  --third-color:#fff;
  --forth-color:#fff;
  --theme:rgb(255, 255, 0);
}
* {
  font-family: "Nunito", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;;
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: capitalize;
  transition: all 0.2s linear;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

html::-webkit-scrollbar {
  width: 0.7rem;
}

html::-webkit-scrollbar-track {
  background: #fff;
}

html::-webkit-scrollbar-thumb {
  background: #27ae60;
  background: var(--green);
  border-radius: .5rem;
}
#theme-toggler {
  position: fixed;
  top: 1rem;
  right: .2rem;
  z-index: 1000;
  height: 5rem;
  width: 5rem;
  line-height: 5rem;
  border-radius: 50%;
  font-size: 2rem;
  cursor: pointer;
  text-align: center;
  color: var(--theme);
}

#theme-toggler:hover {
  color: var(--green);
}
body {
  background: var(--main-bg-color);
  padding-right: 5rem;
}

section {
  padding: 2rem 9%;
  min-height: 100vh;
}

.heading {
  text-align: center;
  padding-bottom: 2rem;
  font-size: 6rem;
  text-transform: uppercase;
  color: var(--second-color);
}

.heading span {
  text-transform: uppercase;
  color: var(--green);
}

.btn {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.8rem 3rem;
  background: var(--second-color);
  color: var(--white-color);
  font-size: 1.7rem;
  cursor: pointer;
}

.btn:hover {
  background: var(--green);
  letter-spacing: 0.2rem;
}

header {
  position: fixed;
  top: 0;
  right: 0;
  min-height: 100vh;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: var(--white-color);
  z-index: 1000;
}

header .navbar {
  -webkit-writing-mode: vertical-lr;
      -ms-writing-mode: tb-lr;
          writing-mode: vertical-lr;
  padding: 1.5rem;
}

header .navbar a {
  padding: 2.5rem;
  font-size: 2rem;
  color: var(--second-color);
}

header .navbar a:hover {
  color: var(--green);
}

#menu-bars {
  position: fixed;
  z-index: 10000;
  top: 1rem;
  left: 1rem;
  padding: 1rem 1.5rem;
  border-radius: 0.5rem;
  color: var(--white-color);
  background: var(--green);
  cursor: pointer;
  font-size: 3rem;
  display: none;
}

.home {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.home .content {
  text-align: center;
  position: relative;
  z-index: 1;
}

.home .content img {
  height: 25rem;
  width: 25rem;
  border-radius: 50%;
  object-fit: cover;
  border: 1rem solid var(--green);
}

.home .content h3 {
  font-size: 4rem;
  color: var(--second-color);
  text-transform: uppercase;
}

.home .content h3 span {
  color: var(--green);
  text-transform: uppercase;
}
.home .content .share {
  position: relative;
  border-radius: 0.5rem;
  padding: 1rem;
}
.home .content .share a i {
  height: 5rem;
  width: 5rem;
  line-height: 5rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  margin: 0.5rem;
  font-size: 2.5rem;
  color: var(--second-color);
  border-radius: 1rem;
}
.home .content .share a i:hover {
  color: var(--green);
}
.home .content p {
  color: var(--second-color);
  font-size: 2.5rem;
  line-height: 2;
}

.about .row {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  align-items: center;
}

.about .row .image {
  text-align: center;
  flex: 1 1 20rem;
}

.about .row .image img {
  width: 30rem;
  border-radius: 0.5rem;
  border: 1rem solid var(--forth-color);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
}

.about .row .content {
  flex: 1 1 55rem;
}

.about .row .content h3 {
  color: var(--second-color);
  font-size: 4rem;
}

.about .row .content h3 span {
  color: var(--green);
}

.about .row .content .info {
  color: var(--third-color);
  font-size: 1.7rem;
  padding: 1rem 0;
  line-height: 2;
}

.about .row .content .box-container {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  padding: 1rem 0;
}

.about .row .content .box-container .box {
  flex: 1 1 20rem;
}

.about .row .content .box-container .box p {
  font-size: 1.7rem;
  color: var(--second-color);
  padding: 0.5rem 0;
  line-height: 2;
}

.about .row .content .box-container .box p span {
  color: var(--green);
}

.about .row .content .box-container {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  padding: 1rem 0;
}

.services .box-container {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.services .box-container .box {
  flex: 1 1 30rem;
  border-radius: 1rem;
  background: var(--white-color);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  padding: 2rem;
  text-align: center;
}

.services .box-container .box img {
  height: 8rem;
}

.services .box-container .box h3 {
  color: var(--second-color);
  font-size: 2.5rem;
  padding-top: 1rem;
}

.services .box-container .box p {
  color: var(--third-color);
  font-size: 1.7rem;
  padding: 1rem 0;
  line-height: 2;
}


.portfolio .box-container {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.portfolio .box-container .box {
  flex: 1 1 30rem;
  border-radius: 0.5rem;
  border: 1rem solid --white-color;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
  height: 25rem;
}

.portfolio .box-container .box img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.portfolio .box-container .box:hover img{
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.portfolio .box-container .box .content {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: -100%;
  background-image: linear-gradient(
    180deg,rgba(0, 60, 255, 0.329),#529dff);
  display: flex;
  align-items: center;
  justify-content: center;
}

.portfolio .box-container .box:hover .content {
  left: 0;
}

.contact .row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.contact .row .image {
  flex: 1 1 40rem;
}

.contact .row .image img {
  width: 100%;
}

.contact .row form {
  flex: 1 1 40rem;
  padding: 1rem 2rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
  background: var(--main-bg-color) ;
}

.contact .row form .inputBox {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.contact .row form .inputBox input,
.contact .row form .box,
.contact .row form textarea {
  width: 100%;
  border-radius: 0.5rem;
  font-size: 1.7rem;
  color: #333;
  padding: 1rem;
  margin: 1rem 0;
  border: 0.1rem solid rgba(0, 0, 0, 0.1);
  text-transform: none;
}

.contact .row form .inputBox input:focus,
.contact .row form .box:focus,
.contact .row form textarea:focus {
  border-color: var(--green);
}

.contact .row form .inputBox input {
  width: 49%;
}

.contact .row form textarea {
  height: 20rem;
  resize: none;
}
.contact .row form small{
  color: red;
  display: block;
}
.contact .row form strong{
  color: red;
  font-size: 1rem;
  display: block;
}
.contact .row form .btn {
  margin: 0.5rem 0;
}

.footer {
  padding: 2.5rem;
  font-size: 2rem;
  color: #666;
  background: var(--white-color);
  text-align: center;
}

.footer span {
  color: var(--green);
}

/* media queries  */
@media (max-width: 768px) {
  html {
    font-size: 55%;
  }

  body {
    padding-right: 0;
  }

  section {
    padding: 2rem;
  }

  header {
    width: 100%;
    left: -120%;
  }

  header.active {
    left: 0%;
  }

  #menu-bars {
    display: inline;
    display: initial;
  }

  header .navbar {
    -webkit-writing-mode: horizontal-tb;
        -ms-writing-mode: lr-tb;
            writing-mode: horizontal-tb;
  }

  header .navbar a {
    display: block;
    font-size: 3rem;
  }
}

@media (max-width: 450px) {
  html {
    font-size: 50%;
  }

  .heading {
    font-size: 4rem;
  }

  .contact .row form .inputBox input {
    width: 100%;
  }
  .services .box-container {
    display: flex;
    flex-wrap: wrap;
  }
  .services .box-container .box{
    flex: 1rem 1 20rem;
  }
}

